import React, {useEffect, useState} from 'react';
import {useParams} from "react-router-dom";
import {elements} from './DataBase.js';
import { Link } from "react-router-dom";

import {ReactComponent as Tppan3} from './maps/Tppan3.svg';
import {ReactComponent as Tppan4} from './maps/Tppan4.svg';
import {ReactComponent as Tppan5} from './maps/Tppan5.svg';
import {ReactComponent as Kkenhus1} from './maps/Kken1.svg';
import {ReactComponent as Kkenhus2} from './maps/Kken2.svg';
import {ReactComponent as Kkenhus3} from './maps/Kken3.svg';
import {ReactComponent as Kkenhus4} from './maps/Kken4.svg';
import {ReactComponent as Kkenhus5} from './maps/Kken5.svg';
import {ReactComponent as Back} from './icons/back.svg';

function getRoomByName(room){
    const found = elements.find(element => {
        return element.room == room;
    });
    return found;
}

function LocationDetails(){
    var mapName;
    var startFloor = "heh";

    const {roomName} = useParams();
    const room = getRoomByName(roomName);

    // Om man tryckt p Tppan kommer vningarna fr Tppan att visas, samma fr Kken
    if(roomName == "Tappan"){
        startFloor = "TP3";
    }
    else if(roomName == "Kakenhus"){
        startFloor = "K2";
    }

    const [currentFloor, setCurrentFloor] = useState(startFloor);
    
    if(room){
        mapName = room.building + room.floor;

    }
    else{
        mapName = roomName;

    }
    
    useEffect(() => {
        if(room){
            document.querySelector("#"+roomName).classList.add("start_buildings");
        }
        }, []);

        function changeFloor(floor){
            setCurrentFloor(floor);
        }

    return(
        <div className="details">
            <Link to="/">
                <div id="tillbakaKnapp">
                <Back/>   
                </div>       
            </Link>

            {(mapName==='Tppan3' || currentFloor=='TP3') &&
                <>
                    <h1 id="headerRoom">Tppan</h1>
                    <Tppan3/>
                </>
            }
            {(mapName==='Tppan4' || currentFloor=='TP4') &&
                <>
                    <h1 id="headerRoom">Tppan</h1>
                    <Tppan4/>
                </>
            }
            {(mapName==='Tppan5' || currentFloor=='TP5') &&
                <>
                    <h1 id="headerRoom">Tppan</h1>
                    <Tppan5/>
                </>
            }
            
            {(mapName==='Kkenhus1' || currentFloor=='K1') &&
                <>
                    <h1 id="headerRoom">Kkenhus</h1>
                    <Kkenhus1/>
                </>
               
            }
            {(mapName==='Kkenhus2' || currentFloor=='K2') &&
                <>
                    <h1 id="headerRoom">Kkenhus</h1>
                    <Kkenhus2/>
                </>
            }
            {(mapName==='Kkenhus3' || currentFloor=='K3') &&
                <>
                    <h1 id="headerRoom">Kkenhus</h1>
                    <Kkenhus3/>
                </>
            }
            {(mapName==='Kkenhus4' || currentFloor=='K4') &&
                <>
                    <h1 id="headerRoom">Kkenhus</h1>
                    <Kkenhus4/>
                </>
            }
            {(mapName==='Kkenhus5' || currentFloor=='K5') &&
                <>
                    <h1 id="headerRoom">Kkenhus</h1>
                    <Kkenhus5/>
                </>
            }
            
            {mapName==='Tappan' &&
            <>
                
                <div id='floorContainer'>
                <button autoFocus onClick={() => changeFloor("TP3")}>Vning 3</button>
                <button onClick={() => changeFloor("TP4")}>Vning 4</button>
                <button onClick={() => changeFloor("TP5")}>Vning 5</button>
                </div>
            </>
            }
            {mapName==='Kakenhus' &&
            <>

                <div id='floorContainer'>
                <button onClick={() => changeFloor("K1")}>Vning 1</button>
                <button autoFocus onClick={() => changeFloor("K2")}>Vning 2</button>
                <button onClick={() => changeFloor("K3")}>Vning 3</button>
                <button onClick={() => changeFloor("K4")}>Vning 4</button>
                <button onClick={() => changeFloor("K5")}>Vning 5</button>
                </div>
            </>
            }
            
            {mapName!==roomName &&
            <>

                <div id="description">
                    <h2>{room.room}</h2>
                    <h2>Vning {room.floor}</h2>
                </div>
                </>
            }
        </div>

    )
}
export default LocationDetails;